/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push } from 'react-router-redux';

import './styles.css';

import { store } from '../../../store';
import { CHOOSE_PURCHASE } from '../../../constants/actionTypes';

class Pricing extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      mobileView: false,
      planType: 'month',
    };
    this.pricing = [
      {
        planName: 'Starter',
        price: null,
        disc_price: 0,
        features: [
          'Unlimited Links',
          'Link traffic',
          'Link Clicks',
          'Click Rates',
          'Integration with Facebook,Instagram,Google Sheets,YouTube,Patreon and many more',
        ],
      },
      {
        planName: 'Pro Pack',
        price: '449',
        disc_price: ' 49',
        type: '/month',
        features: [
          'Unlimited Links',
          'Link Priority',
          'Link icons',
          'NewsLetter',
          'Link traffic(Daily,Weekly,Totals)',
          'Link Clicks(Daily,Weekly,Totals)',
          'Click Rates(Daily,Weekly,Totals)',
          'Change App logo',
          'Assign your own header',
          'Customize Page styling',
          'Integration with Facebook,Instagram,Google Sheets,YouTube,Patreon and many more',
          'Support',
          'Priority Support',
          'Team Access',
        ],
      },
      {
        planName: 'Pro Pack',
        price: '4499',
        disc_price: ' 499',
        type: '/year',
        features: [
          'Unlimited Links',
          'Link Priority',
          'Link icons',
          'NewsLetter',
          'Link traffic(Daily,Weekly,Totals)',
          'Link Clicks(Daily,Weekly,Totals)',
          'Click Rates(Daily,Weekly,Totals)',
          'Change App logo',
          'Assign your own header',
          'Customize Page styling',
          'Integration with Facebook,Instagram,Google Sheets,YouTube,Patreon and many more',
          'Support',
          'Priority Support',
          'Team Access',
        ],
      },

    ];
  }

  componentWillMount() {
    window.addEventListener('resize', this.resize.bind(this));
    this.resize();
    if(this.props.plans){
      this.plans = this.props.plans
    }
  }
  componentWillReceiveProps(np){
    if(np.plans){
      this.plans = np.plans.packs
      this.plans.forEach(p => {
        if(p.period === 'monthly'){
          this.pricing[1].disc_price = p.price 
          this.pricing[1].plan_id = p.plan_id
        }
        if(p.period === 'yearly'){
          this.pricing[2].disc_price = p.price 
          this.pricing[2].plan_id = p.plan_id
        }
      })
      this.setState({render:!this.state.render})
    }
  }
  resize() {
    this.setState({ mobileView: window.innerWidth <= 600 });
  }

  choosePlan(plan) {
    this.props.choosePlan(plan);
    store.dispatch(push('/purchase'));
  }


  renderPricing() {
    return (
      this.pricing.map((p, i) => (
        <div className={i !== 1 ? 'single-price' : 'single-price pro-pack-bg'} key={i}>
          <div className="deal-top div-2">
            <h3>
              {p.planName}

            </h3>
            {
              p.price
                ? (
                  <strike>
                Rs.
                    {p.price}
                  </strike>
                ) : null
            }

            {
              p.disc_price !== 0 ? (
                <h4>
              Rs.
                  {p.disc_price}
                  <span>

                    {p.type}
                  </span>
                </h4>
              ) : null
            }

          </div>
          <div className="deal-bottom">
            <ul className="deal-item">
              {
                p.features.map((f, i) => (
                  <li className="b" key={i + 1000}>{`${f}`}</li>
                ))
              }
            </ul>
            <div className="btn-area">
              {
                i ===0 ?
                <a className="div-2 purchase-btn" onClick={() => store.dispatch(push('/signup'))}>Sign up</a>
                :
                <a className="div-2 purchase-btn" onClick={() => this.choosePlan(p)}>Coming Soon</a>
              }
              
            </div>
          </div>
        </div>
      ))

    );
  }


  render() {
    const { planType } = this.state;
    return (


      <div className="main-div-width pb5" id="pricing">

        <div className="tc center  home-page-header flex flex-column items-center">

          <h1 className="tc">
                 Pick you plan or start free
          </h1>

        </div>
        <div className="pricing-area">
          {this.renderPricing()}


        </div>

      </div>


    );
  }
}


const mapStateToProps = (state) => ({
  plans : state.PurchaseReducers.plans

});

const mapDispatchToProps = (dispatch) => ({
  choosePlan: (plan) => dispatch({ type: CHOOSE_PURCHASE, payload: plan }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Pricing);
